"use client";

import { useState, useCallback } from "react";
import { useToast } from "@/shared/ui/use-toast";
import { TeamsService } from "../api/teams.service";
import { Team, CreateTeamDto, TeamMember } from "@/entities/team/model/types";

interface UseTeamsState {
  teams: Team[];
  currentTeam: Team | null;
  teamMembers: TeamMember[];
  isLoading: boolean;
  error: string | null;
}

export function useTeams() {
  const [state, setState] = useState<UseTeamsState>({
    teams: [],
    currentTeam: null,
    teamMembers: [],
    isLoading: false,
    error: null,
  });
  const { toast } = useToast();

  const createTeam = useCallback(
    async (data: CreateTeamDto) => {
      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));
        const team = await TeamsService.createTeam(data);
        setState((prev) => ({
          ...prev,
          teams: [...prev.teams, team],
          currentTeam: team,
          isLoading: false,
        }));

        toast({
          title: "–£—Å–ø–µ—à–Ω–æ!",
          description: "–ö–æ–º–∞–Ω–¥–∞ —Å–æ–∑–¥–∞–Ω–∞",
        });

        return team;
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –∫–æ–º–∞–Ω–¥—ã";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));

        toast({
          variant: "destructive",
          title: "–û—à–∏–±–∫–∞!",
          description: message,
        });

        throw error;
      }
    },
    [toast]
  );

  const joinTeam = useCallback(
    async (teamId: number) => {
      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));
        await TeamsService.joinTeam({ team_id: teamId });
        const team = await TeamsService.getTeamById(teamId);

        setState((prev) => ({
          ...prev,
          teams: [...prev.teams, team],
          currentTeam: team,
          isLoading: false,
        }));

        toast({
          title: "–£—Å–ø–µ—à–Ω–æ!",
          description: "–í—ã –ø—Ä–∏—Å–æ–µ–¥–∏–Ω–∏–ª–∏—Å—å –∫ –∫–æ–º–∞–Ω–¥–µ",
        });

        return team;
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø—Ä–∏—Å–æ–µ–¥–∏–Ω–µ–Ω–∏–∏ –∫ –∫–æ–º–∞–Ω–¥–µ";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));

        toast({
          variant: "destructive",
          title: "–û—à–∏–±–∫–∞!",
          description: message,
        });

        throw error;
      }
    },
    [toast]
  );

  const fetchMyTeams = useCallback(async () => {
    try {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      const teams = await TeamsService.getMyTeams();
      setState((prev) => ({ ...prev, teams, isLoading: false }));
      return teams;
    } catch (error) {
      const message =
        error instanceof Error
          ? error.message
          : "–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ —Å–ø–∏—Å–∫–∞ –∫–æ–º–∞–Ω–¥";
      setState((prev) => ({ ...prev, error: message, isLoading: false }));
      throw error;
    }
  }, []);

  const getTeamById = useCallback(async (teamId: number) => {
    try {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      const team = await TeamsService.getTeamById(teamId);
      setState((prev) => ({ ...prev, currentTeam: team, isLoading: false }));
      return team;
    } catch (error) {
      const message =
        error instanceof Error ? error.message : "–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –∫–æ–º–∞–Ω–¥—ã";
      setState((prev) => ({ ...prev, error: message, isLoading: false }));
      throw error;
    }
  }, []);

  const getTeamByCode = useCallback(async (code: string) => {
    try {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      const team = await TeamsService.getTeamByCode(code);
      setState((prev) => ({ ...prev, currentTeam: team, isLoading: false }));
      return team;
    } catch (error) {
      const message =
        error instanceof Error ? error.message : "–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –∫–æ–º–∞–Ω–¥—ã";
      setState((prev) => ({ ...prev, error: message, isLoading: false }));
      throw error;
    }
  }, []);

  const getTeamMembers = useCallback(async (teamId: number) => {
    try {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      const members = await TeamsService.getTeamMembers(teamId);
      setState((prev) => ({ ...prev, teamMembers: members, isLoading: false }));
      return members;
    } catch (error) {
      const message =
        error instanceof Error
          ? error.message
          : "–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ —É—á–∞—Å—Ç–Ω–∏–∫–æ–≤ –∫–æ–º–∞–Ω–¥—ã";
      setState((prev) => ({ ...prev, error: message, isLoading: false }));
      throw error;
    }
  }, []);

  return {
    ...state,
    createTeam,
    joinTeam,
    fetchMyTeams,
    getTeamById,
    getTeamByCode,
    getTeamMembers,
  };
}
